import mm1img from'../img/mm1.jpg';

// for skybox images
import skyTop from'../img/skybox/top.png';
import skyBottom from'../img/skybox/bottom.png';    
import skyLeft from'../img/skybox/left.png';
import skyRight from'../img/skybox/right.png';
import skyFront from'../img/skybox/Front.png';
import skyBack from'../img/skybox/back.png';
import { TextureLoader, Vector4 } from 'three';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js';

// import images for solar system
import earthImg from'../solarImg/earth.jpg';
import jupiterImg from'../solarImg/jupiter.jpg';
import marsImg from'../solarImg/mars.jpg';
import mercuryImg from'../solarImg/mercury.jpg';
import neptuneImg from'../solarImg/neptune.jpg';
import saturnringImg from'../solarImg/saturn ring.png';
import saturnImg from'../solarImg/saturn.jpg';
import starsImg from'../solarImg/stars.jpg';
import sunImg from'../solarImg/sun.jpg';
import uranusringImg from'../solarImg/uranus ring.png';
import uranusImg from'../solarImg/uranus.jpg';
import venusImg from'../solarImg/venus.jpg';


//for model import 1st do this (after save this line see in dist folder this file has been created there), after that import gltf
const lmbgModelUrl=new URL('../assets/models2/Lamborghini_Aventador.obj',import.meta.url);
const monkeyUrl=new URL('../assets/assets1/monkey.glb',import.meta.url);

const renderer=new THREE.WebGL1Renderer();
renderer.shadowMap.enabled=true;

renderer.setSize(window.innerWidth,window.innerHeight);
document.body.appendChild(renderer.domElement);
const scene=new THREE.Scene();
const camera=new THREE.PerspectiveCamera(45,window.innerWidth/window.innerHeight,0.1,1000);
// for orbit control class, create an instance
const orbit=new OrbitControls(camera,renderer.domElement);
const axesHelper=new THREE.AxesHelper(3);
scene.add(axesHelper);
//like this alteranate of above two positions
camera.position.set(-90,140,140);
// update orbit here
orbit.update();


// start drawing from here to all the planets
// add texture loader
const textureLoader=new THREE.TextureLoader();
// draw sun 1st
const sunGeom=new THREE.SphereGeometry(16,30,30);
const sunMat=new THREE.MeshBasicMaterial({map: textureLoader.load(sunImg)});
const sun=new THREE.Mesh(sunGeom,sunMat);
scene.add(sun);


// draw all plnets using this function here
function drawPlanet(size,texture,position,ring)
{
    //draw mecury, use mat as mesh std and add light source
const geom=new THREE.SphereGeometry(size,30,30);
const mat=new THREE.MeshStandardMaterial({map: textureLoader.load(texture)});
const mesh=new THREE.Mesh(geom,mat);
// mercury object create
const obj=new THREE.Object3D();
obj.add(mesh);

if(ring)
{
// saturn ring
// draw saturn
const ringGeom=new THREE.RingGeometry(ring.innerRadius,ring.outerRadius,32);
const ringMat=new THREE.MeshBasicMaterial({map: textureLoader.load(ring.texture),side:THREE.DoubleSide});
const ringMesh=new THREE.Mesh(ringGeom,ringMat);
obj.add(ringMesh);
ringMesh.position.x=position;
ringMesh.rotation.x=0.5*Math.PI;
}

scene.add(obj);
mesh.position.x=position;
return {mesh,obj}
}
//call fun to draw mercury
const mercury=drawPlanet(3.2,mercuryImg,28);
// draw venus
const venus=drawPlanet(5.8,mercuryImg,44);
//earth
const earth=drawPlanet(6,mercuryImg,62);
//mars
const mars=drawPlanet(4,mercuryImg,78);
//jupiter
const jupiter=drawPlanet(12,mercuryImg,100);

// draw saturn by call that fun
const saturn=drawPlanet(10,saturnImg,138,{
    innerRadius:10,outerRadius:20,texture:saturnringImg
});
//uranus
// draw saturn by call that fun
const uranus=drawPlanet(7,saturnImg,176,{
    innerRadius:7,outerRadius:12,texture:saturnringImg
});
// neptune
const neptune=drawPlanet(7,mercuryImg,200);
//pluto
const pluto=drawPlanet(2.8,mercuryImg,216);


// add light for mecury
const pointLight=new THREE.PointLight(0xFFFFFF,2,300);
scene.add(pointLight);

const ambtLight1=new THREE.AmbientLight(0x333333);
scene.add(ambtLight1);
// set bg  simple bg image here
const cubeTextLoader=new THREE.CubeTextureLoader();
scene.background=cubeTextLoader.load([starsImg,starsImg,starsImg,starsImg,starsImg,starsImg]);

// rotation start, by animation fun
function animate(time)
{
    //rotate sun
    sun.rotateY(0.004);
    // self rotation of all planets
    mercury.mesh.rotateY(0.004);
    venus.mesh.rotateY(0.002);
    earth.mesh.rotateY(0.02);
    mars.mesh.rotateY(0.018);
    jupiter.mesh.rotateY(0.04);    
    saturn.mesh.rotateY(0.038);
    uranus.mesh.rotateY(0.03);
    neptune.mesh.rotateY(0.032);
    pluto.mesh.rotateY(0.008);

    // arround sun rotation of each planets
    mercury.obj.rotateY(0.04);  
    venus.obj.rotateY(0.015);
    earth.obj.rotateY(0.01);  
    mars.obj.rotateY(0.008);   
    jupiter.obj.rotateY(0.002); 
    saturn.obj.rotateY(0.0009);
    uranus.obj.rotateY(0.0004); 
    neptune.obj.rotateY(0.0001); 
    pluto.obj.rotateY(0.00007); 

    renderer.render(scene, camera);
}
renderer.setAnimationLoop(animate);
window.addEventListener('resize',function(){
    camera.aspect=window.innerWidth/window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth,window.innerHeight);
});



